/* src/styles/SearchBar.css */
.search-bar {
    background-color: #f2f2f2;
    padding: 1rem;
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    width: 100%;
}

.search-bar input {
    flex-grow: 1;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.search-bar button {
    padding: 0.5rem 1rem;
    border: none;
    background-color: #5cb85c;
    color: white;
    border-radius: 4px;
    cursor: pointer;
}

.search-bar button:hover {
    background-color: #4cae4c;
}

/* Responsive Design */
@media (max-width: 768px) {
    .search-bar {
        flex-direction: column;
        gap: 1rem;
    }

    .search-bar button {
        width: 100%;
    }
}